.part {
  max-height: fit-content;
  height: 100vh;
  position: relative;

  width: 100%;
}
.top-section {
  width: 100%;
  height: 50vh;

  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;

  background: url('../../assets/aj.png') center right no-repeat;
}
.top-section h2 {
  position: absolute;
  left: 10%;
  font-size: 30px;
  font-family: genbold;
}
.top-section h2 span {
  color: var(--error);
  font-family: genbold;
  font-size: 30px;
}

.bottom-section {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 4rem;
  line-height: 1.7rem;
}
.righ {
  width: 70%;
  margin-right: 2rem;

  font-family: genorm;
  font-size: 14px;
}
.righ p {
  margin-top: 10px;
}
.head {
  font-size: 26px;
  font-family: genbold;
}
.righ .s {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 20%;
}
.righ .s .innerline {
  height: 100%;
  width: 15px;
  background-color: var(--b);
  margin-right: 2rem;
}
.left {
  width: 30%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.left .logo img {
  height: 100%;
  width: 150px;
}
.left .iconss {
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.iconss i {
  margin-left: 20px;
}
